* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  cursor: none; 
}
body,
html {
  height: 100%;
  font-size: 16px;
  line-height: 1.5;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
#root {
  display: flex;
  width: 100%;
  height: 100%;
}